import Noty from "noty";

Noty.overrideDefaults({
  theme: "mint",
  closeWith: ["click", "button"],
});

const iconsNames = {
  error: "error",
  success: "check_circle",
  warning: "warning",
  info: "help_outline",
};

export default function Toast(message, type) {
  this.message = message;
  this.type = type;
  this.icon = `<span class="material-icons-round">${iconsNames[type]}</span>`;
  this.text = `<div class="flex gap-1">${this.icon}${message}</div>`;
  this.toast = new Noty({
    type,
    text: this.text,
    progressBar: true,
    timeout: 4000,
  });

  this.show = function () {
    this.toast.show();
  };
}
