export default function initTabs() {
  function show(tabId) {
    const tabWrapper = document.querySelector(".tabs-wrapper");
    if (tabWrapper) {
      [...tabWrapper.querySelectorAll(".tab")].forEach((tab) => {
        tab.classList.remove("active");
        if (tab.getAttribute("id") == tabId) tab.classList.add("active");
      });
    }
  }

  const tabLinks = document.querySelectorAll(".nav-tabs__link");
  [...tabLinks].forEach((tabLink) => {
    tabLink.addEventListener("click", (e) => {
      e.preventDefault();
      const tabId = tabLink.dataset.tabId;
      show(tabId);
      [...tabLinks].forEach((link) => {
        if (link.classList.contains("active")) link.classList.remove("active");
      });
      tabLink.classList.add("active");
    });
  });
}
